import Image from './../../Elements/Media/Image'
import Video from "./../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
  const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_stt_multi_images_ico_bottom' : 'tb_stt_multi_images_ico'
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  return (<div className="tb_stt_media_wrap">
    {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
    {autoPlay ?
      <Video VideoClass="tb_stt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={200} />
      :
      <Image ImageClass="tb_stt_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={200} />
    }
  </div>)
}
export default Media